.location_container {
  background-color: #f4f6f9;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-top: 20px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 60px;
}

.title {
  font-size: calc(18px + 2vmin);
  text-align: center;
  color: #5f5f5f;
}

.divider {
  border: solid 0 #5f5f5f;
  background-color: #cfcfcf;
  height: 1px;
  width: 100%;
}

.image_location {
  margin-top: 4px;
}

.shortcut_container {
  display: flex;
  flex-direction: row;
  margin-top: 8px;
}

.shortcut_style {
  background-color: white;
  color: #6f6f6f;
  border: solid 1px #cfcfcf;
  padding: 4px 4px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 10px;
  width: 92%;
}

.location_text1 {
  text-align: center;
  margin-top: 20px;
  font-size: 14px;
  color: #7f7f7f;
}

.location_text2 {
  text-align: center;
  font-size: 18px;
  margin-top: 8px;
  color: #5f5f5f;
}

.location_detail_container {
  background-color: white;
  color: #5f5f5f;
  display: flex;
  flex-direction: row;
  margin-top: 16px;
  padding: 1.6em 1em;
  font-size: 13px;
  line-height: 1.4em;
}